.main-form {
    display: flex;
    justify-content: center;
  }
  .flex-form {
    display: flex;
    flex-direction: column;
    margin: 1rem;
  }
  
  form input {
    border-radius: 10px;
    padding: 0.3rem;
    margin-top: 0.2rem;
  }
  form label {
    padding-bottom: 1rem;
  }
  .btn-2 {
    border: none;
  
    border-radius: 28px;
    font-family: Arial;
    color: #ffffff;
    font-size: 20px;
    background: #05668d;
    padding: 10px 60px 10px 60px;
    text-decoration: none;
    transition: 0.3s;
  }
  
  .btn-2:hover {
    background: #000;
    text-decoration: none;
  }
  form {
    background-color: rgba(57, 57, 255, 0.308);
    padding: 1rem;
  }
  