nav {
  display: flex;

  background: #1a335e;
  position: relative;
  justify-content: space-around;
  text-align: center;
  padding: 1.4rem 0 1.4rem;
}

nav .icon {
  font-size: 32px;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

nav ul {
  display: flex;
  list-style: none;
  align-items: center;
  background-color: #537696;

  border-radius: 50px;
}

nav ul li a {
  padding: 12px;
  border-radius: 50px;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

nav ul li a:hover {
  background-color: #8a98b171;
}

nav .search {
  display: flex;
  margin: auto 0;
  height: 35px;
  line-height: 35px;
}

nav .search input {
  border: none;
  border-radius: 25px;
  outline: none;
  background-color: #537696;
  height: 100%;
  padding: 0 10px;
  font-size: 20px;
  width: 350px;
}

nav .search input::-webkit-input-placeholder {
  color: #fff;
}

nav .search input:-ms-input-placeholder {
  color: #fff;
}

nav .search input::placeholder {
  color: #fff;
}

nav .bar {
  position: relative;
  margin: auto;
  display: none;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  display: none;
}

.search_material {
  color: #fff;

  position: absolute;
  margin-left: 19rem;
  margin-top: 0.3rem !important;
  cursor: pointer;
  z-index: 1;
}

@media screen and (max-width: 1250px) {
  nav {
    display: block;
    padding: 0;
  }
  nav .icon {
    display: inline;
    padding: 15px 30px;
  }
  nav .search {
    width: 100%;
    display: inline-flex;
    justify-content: center;
    margin-bottom: 15px;
    padding-top: 15px;
  }
  nav .search input {
    width: 90%;
    height: 30px;
  }

  nav ul {
    display: flex;
    flex-direction: column;
    height: 0;
    visibility: hidden;
    background-color: #1a335e;
  }

  nav ul li a {
    font-size: 28px;
    padding: 25px;

    white-space: nowrap;
    display: block;

    text-align: center;
  }

  nav .bar {
    display: block;
    position: absolute;
    top: 15px;
    right: 20px;
    cursor: pointer;
  }
  nav .bar #times {
    display: none;
  }
  #check:checked ~ nav .bar #times {
    display: block;
  }
  #check:checked ~ nav .bar #bars {
    display: none;
  }
  #check:checked ~ nav ul {
    visibility: visible;
    height: 400px;
  }
}

/* Laptop Responsiveness */
@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 1) {
  nav ul li a {
    font-size: 12px;
  }

  .search_material {
    margin-top: -4px;
  }

  nav .search {
    margin-left: 15px;
  }
}

